const { ccclass, property } = cc._decorator;

// Define a custom class for your data structure
class CustomItem {
    constructor(public name: string, public sprite: cc.SpriteFrame) {}
}

@ccclass
export default class AtoZLetterData extends cc.Component {
    @property({
        type: [cc.String], // Specify the type as an array of strings
        tooltip: 'Names for custom items'
    })
    letter: string[] = [];

    @property({
        type: [cc.SpriteFrame],
        tooltip: 'Sprites for custom items'
    })
    letterSprite: cc.SpriteFrame[] = [];
 

    @property({
        tooltip: 'Array of custom items'
    })
    customItems: CustomItem[] = [];


    onLoad() {
        this.customItems = this.letterSprite.map((sprite, index) => new CustomItem(this.letter[index], sprite));
    }
}
